    padding: 5px;
    background-color: #444;
    color: white;
    border: 1px solid #555;
    overflow: hidden;
    text-align: center;

    img{
        width: 100%;
    }

    .info{
        font-size: 0.8em;
        line-height: 1.3em;

        h3{
            margin: 10px;
            height: 30px;
            font-weight: 500;
        }
        h4{
            margin: 10px;
            height: 30px;
            font-weight: 600;

        }
    }
}